input, textarea {
  border: 2px solid #F5F5F5;
  border-radius: 0.6rem;
  font-size: 1.2rem;
  outline: 0;
  padding: 1rem;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  width: calc(100% - 2rem);
}

input:focus, textarea:focus {
  border-color: #E5E5E5;
}

.radio {
  -ms-flex-align: center;
      align-items: center;
  color: #555;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-top: 0.6rem;
}

.radio .im {
  margin-right: 0.6rem;
}

.radio.selected {
  color: #FF512F;
}



